import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})

export class ReqresApiService {
 	serverURL = "https://reqres.in/api/";

  	constructor(private http: HttpClient) { }

	login(url, loginInfo) : Promise<{}>{
		return this.http.post(this.serverURL + url, loginInfo)
              .toPromise()
              .then((data) => data);
	}

	getList(url) : Promise<{}>{
		return this.http.get(this.serverURL + url)
              .toPromise()
              .then((data) => data);
	}

	create(url, userInfo) : Promise<{}>{
		return this.http.post(this.serverURL + url, userInfo)
              .toPromise()
              .then((data) => data);
	}

	update(url, userInfo) : Promise<{}>{
		return this.http.put(this.serverURL + url, userInfo)
              .toPromise()
              .then((data) => data);
	}

	delete(url) : Promise<{}>{
		return this.http.delete(this.serverURL + url)
              .toPromise()
              .then((data) => data);
	}
}
