import { Component, OnInit } from '@angular/core';
import { ReqresApiService } from '../reqres-api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.css']
})
export class LoginPageComponent implements OnInit {

  constructor(private reqresApiService: ReqresApiService, private router: Router) { }
  enableRegisterPage : boolean = false;
  isLoading : boolean = false;
  loginInfo  = {
  	"email" : '',
  	"password": ''
  };

  onLogin(loginType){
  	let url = loginType == 'login' ? "login" : "register";
  	let regX = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
  	if(this.loginInfo.email != '' && this.loginInfo.password != ''){
  		if(regX.test(this.loginInfo.email)){
  			this.isLoading = true;
  			this.reqresApiService.login(url, this.loginInfo)
  				.then((response: any) => {
  					this.isLoading = false;
  					this.router.navigateByUrl('users');
  				},function(error) {
            this.isLoading = false;
  					alert('Something went wrong')
  				});
  		}
  		else{
  			alert("Please enter valid email id");
  		}
  	}
  	else{
  		alert("Please enter valid input");
  	}
  }

  ngOnInit() {
  }

}
