import { Component, OnInit } from '@angular/core';
import { ReqresApiService } from '../reqres-api.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-list-page',
  templateUrl: './list-page.component.html',
  styleUrls: ['./list-page.component.css']
})
export class ListPageComponent implements OnInit {

 	users : any[] = [];
  newUserInfo = {
    "name": '',
    "job": '',
  }
  isLoading : boolean = false;
  showAddUser : boolean = false;
  onCreateUser : boolean = false;
  createInProgress : boolean = false;
  activePage : number = 0;
  selectedUser : any = {};
  constructor(private reqresApiService: ReqresApiService, private router: Router) { }
  
  onCreate(){
    if(this.newUserInfo.name != '' && this.newUserInfo.job != ''){
      let url = "users";
      this.createInProgress = true;
      this.reqresApiService.create(url, this.newUserInfo)
          .then((response: any) => {
            this.createInProgress = false;
            this.showAddUser = false;
            alert("Successfully created a record " + response.id);
          },function(error) {
            this.createInProgress = false;
            alert('Something went wrong')
          });
    }
    else{
      alert("Please enter valid input");
    }
  }

  onEdit(user){
    this.selectedUser = user;
  }

  onListUser(pageNo){
    let url = "users?page="+ pageNo;
    this.reqresApiService.getList(url)
      .then((response: any) => {
        this.isLoading = false;
        this.users = response.data;
      }, function(error) {
        this.isLoading = false;
        alert('Something went wrong')
    });
  }

  onSaveEdit(user){
    let url = "users/" + user.id;
    let updatedInfo = {
      "name": user.first_name,
      "job": user.last_name
    }
    if(user.name != '' && user.job != ''){
      this.reqresApiService.update(url, updatedInfo)
      .then((response: any) => {
          this.selectedUser = {};
          alert("Successfully Updated")
        }, function(error) {
          this.selectedUser = {};
          alert('Something went wrong')
      });
    }
    else{
      alert("Please enter valid input");
    }
  }

  onCancelEdit(){
    this.selectedUser = {};
  }

  onDelete(user){
    let url = "users/" + user.id;
    this.reqresApiService.delete(url)
    .then((response: any) => {
        alert("Successfully deleted")
      }, function(error) {
        alert('Something went wrong')
    });
  }

  onChangePage(page){
    this.activePage = page;
    this.onListUser(page);
  }

  onLogout(){
    this.router.navigateByUrl('login');
  }

  ngOnInit() {
    this.isLoading = true;
    this.activePage = 1;
    this.onListUser(1);
  }

}
